var that;
import common from '@/store/common.js'
// 这是一个用不了的插件，要付费
// const card = uni.requireNativePlugin('DC-CardRecognize');
export default{
	data() {
		return {
			title: 'input',
			focus: false,
			name: "",
			certificationId:"",
			src:"../../../../static/me/photo.png",
			httpUrl:"https://aip.baidubce.com/rest/2.0/ocr/v1/general?access_token=",
			imageBase64Str:"",
			access_token:"",
			imgList: [],
			modalName: null,
			index:-1,
			APIKey: 'iiZYcBEgl437fmYiC6ErYE4c',
			SecretKey: 'xDToNT5kg702YPvFC85tITLBb7sB4g8l'

		}
	},
	onLoad() {
		// var that=this;
		// token验证
		this.getAccess_token();
	},
	methods: {
		certificate() {
			console.log(common.getGlobalUserInfo().ID,this.name,this.certificationId);
			var flag = this.verifyData();
			if(flag){
				this.getIDCard();
				uni.request({
					url: this.apiServer + '/customer/setCertificationId',
					method: 'POST',
					dataType: "json",
					data: {
						"customerId": common.getGlobalUserInfo().ID,
						"name": this.name,
						"certificationId": this.certificationId
					},
					success: (res) => {
						const result = res.data
						console.log("后台返回数据",result)
						if(result.status == 500){
							this.$api.msg('网络错误');
						}
						if(result == false){
							this.$api.msg('操作失败，请刷新重试');
						}
						if(result == true){
							this.$api.msg('操作成功');
							uni.navigateTo({
								url: "/pages/user/user",
							});
						}
					},
					fail: res => {
						this.$api.msg("网络错误")
					}
				});
			}
		},
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		//选择图片
		ChooseImage() {
			
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album','camera'], //从相册选择
				success: (res) => {
					if (res.tempFilePaths.length == 0) {
						this.imgList = res.tempFilePaths
						console.log('2')
						console.log("this.imgList.length", this.imgList.length)
					} else {
						this.imgList = this.imgList.concat(res.tempFilePaths)
						this.urlTobase64(res.tempFilePaths[0]);
						console.log('1')
					}
				}
			});
		},
		//转base64码
		urlTobase64(url) {
			uni.request({
				url: url,
				method: 'GET',
				responseType: 'arraybuffer',
				success: res => {
					let base64 = wx.arrayBufferToBase64(res.data); //把arraybuffer转成base64
					// this.imageBase64Str = base64 = 'data:image/jpeg;base64,' + base64; //不加上这串字符，在页面无法显示
					this.imageBase64Str = base64;
					// console.log(this.imageBase64Str);
				}
			});
		},
		//预览图片
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		//删除图片
		DelImg(e) {
			uni.showModal({
				title: '删除照片',
				content: '确定要删除这张照片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		getAccess_token(){
			var that = this;
			var accessToken= that.cache("accessToken",null,null) || '';
			console.log(accessToken)
			if(!accessToken){
				uni.request({
					url: "https://aip.baidubce.com/oauth/2.0/token",
					method: 'POST',
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					data:{
						grant_type: "client_credentials",
						client_id: this.APIKey,
						client_secret: this.SecretKey
					},
					success: function(res) {
						console.log(res.data.access_token);
						uni.setStorageSync("Baidu",res.data);
						that.access_token=res.data.access_token;
						that.cache("accessToken",res.data.access_token,null) || '';
					}
				})
			}else{
				that.access_token=accessToken;
			}

		},
		//缓存,默认有效期28天
		cache:function(key, value, seconds) {
			var timestamp = Date.parse(new Date()) / 1000
			 console.log(timestamp+"==="+key)
			if (key && value === null) {
				//删除缓存
				//获取缓存
				var val = uni.getStorageSync(key);
				var tmp = val.split("|")
				if (!tmp[1] || timestamp >= tmp[1]) {
					console.log("key已失效")
					uni.removeStorageSync(key)
					return ""
				} else {
					console.log("key未失效")
					return tmp[0]
				}
			} else if (key && value) {
				//设置缓存
				if (!seconds) {
					var expire = timestamp + (3600 * 24 * 28)
				}else{
					var expire = timestamp + seconds
				}
				value = value + "|" + expire
				uni.setStorageSync(key, value);
			} else {
				console.log("key不能空")
			}
		},
		getIDCard() {
			var flag=this.verifyData();
			var that = this;
			if(flag){
				console.log("百度名片识别接口处理开始")
				//百度名片识别接口处理开始
				uni.showLoading({
					mask: true,
					title: "识别中，请等待"
				});
				uni.request({
					url: "https://aip.baidubce.com/rest/2.0/ocr/v1/idcard?access_token="+that.access_token,
					method: 'POST',
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					data: {
						image: that.imageBase64Str,
						id_card_side:"front"
					},
					success: function(res) {
						console.log(res);
						if(!res.data.error_code){
							var IdCard=res.data.words_result.公民身份号码.words;
							var userName=res.data.words_result.姓名.words;
							console.log(res.data.words_result.公民身份号码.words)
							if(IdCard==that.certificationId&&userName==that.name){
								console.log("身份验证成功");
								uni.showToast({
									icon: 'success',
									title: '身份验证成功'
								});
							}else{
								uni.showToast({
									icon: 'none',
									title: '身份信息有误请审核信息'
								});
							}
						}else{
							that.$api.msg('请求失败请稍后重试');
							// console.log(res);
						}
						
					},
					complete() { 
						uni.hideLoading();
					}
				})
			}
		
		},
		//校验输入的数据
		verifyData() {
			var flag = true;
			if (!this.name) {
				flag = false;
				uni.showToast({
					icon: 'none',
					title: '姓名不能为空'
				});
				return false;
			}
			if (!this.certificationId) {
				flag = false;
				uni.showToast({
					icon: 'none',
					title: '身份证号不能为空'
				});
				return false;
			}
			var re15 = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$/;
			var re18 = /^[1-9](\d{5})(19|20)(\d{2})((0[1-9])|10|11|12)(([0-2][1-9])|10|20|30|31)(\d{3})(\d|X|x)$/;
			var res = (re15.test(this.certificationId) || re18.test(this.certificationId));  
			if(res==false){
				flag = false;
			    uni.showToast({
			    	icon: 'none',
			    	title: '身份证号填写有误'
			    }); 
			    return false;  
			} 
			if(!this.imageBase64Str){
				flag = false;
				uni.showToast({
					icon: 'none',
					title: '请上传身份证照片含	头像面'
				}); 
				return false;  
			}
			return flag;

		}
	}
}
